.about-info {
  width: calc(100% - 250px);
  max-width: 980px;
  margin: 0 auto;
  position: relative;
  margin-top: 0;
  padding-left: 220px;
  padding-right: 30px;
  margin-bottom: 85px;
  background: url("../img/year.png") no-repeat;
  background-size: 220px 317px;
  background-position: 0 5px;
}

.about-info h2 {
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  margin-bottom: 25px;
}

.about-info p {
  font-size: 16px;
  line-height: 22px;
  font-family: $font-mono;
  margin-top: 0;
}

.about-info button {
  background-color: #ffffff;
  border: 1px solid $bg-blue;
  border-radius: 5px;
  width: 178px;
  height: 43px;
  color: $bg-blue;
  text-transform: uppercase;
  margin-top: 25px;
  margin-bottom: 25px;
  cursor: pointer;
}

.about-info button:hover {
  transition: all 0.5s linear;
  background-color: $bg-blue;
  color: #ffffff;
}

@media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
  .about-info {
    background-image: url("../img/year@2x.png");
  }
}

@media (max-width: 1023px) {
  .about-info {
    margin-top: 0;
    background: url("../img/year-tablet.png") no-repeat;
    background-size: 244px 362px;
    background-position: -25px 0;
    margin-bottom: 0;
    padding-bottom: 62px;
    padding-left: 185px;
  }

  .about-info h2 {
    font-size: 32px;
    margin-top: 35px;
  }

  .about-info div {
    width: 535px;
  }

  .about-info span {
    display: none;
  }

  .about-info p:last-of-type::after {
    content: "..";
    display: inline;
  }

  .about-info button {
    margin-top: 25px;
  }

  @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
    .about-info {
      background-image: url("../img/year-tablet.png");
    }
  }
}

@media (max-width: 767px) {
  .about-info {
    width: calc(100% - 30px);
    padding-left: 15px;
    padding-right: 15px;
    background: none;
  }

  .about-info div {
    width: 288px;
    margin: 0;
  }

  .about-info h2 {
    font-size: 28px;
    margin-top: 30px;
    margin-bottom: 0;
  }

  .about-info p {
    margin-top: 25px;
    margin-bottom: 30px;
  }

  .about-info button {
    margin-top: 0;
    margin-bottom: 0;
  }
}
