.form {
  position: relative;
  background-color: $bg-blue;
  width: 100%;
  color: #ffffff;
  padding-bottom: 100px;
  background-image: url("../img/airplane-desktop.png"), url("../img/triangle-group.png");
  background-size: 511px 288px, 635px 510px;
  background-repeat: no-repeat;
  background-position: calc(50% - 350px) 160px, calc(50% - 450px) 80px;
}

.form h2 {
  width: 50%;
  padding-top: 95px;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  position: relative;
  left: 50%;
}

.form p {
  position: relative;
  left: 50%;
  font-size: 16px;
  width: 50%;
  line-height: 22px;
  font-family: $font-mono;
  margin-bottom: 52px;
  margin-top: 5px;
}

.form form {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  max-width: 545px;
  width: calc(50% - 30px);
  position: relative;
  left: 50%;
}

.form input,
.form textarea {
  font-family: $font-mono;
  font-size: 16px;
  box-sizing: border-box;
  padding: 16px;
  background-color: $bg-input;
  color: #ffffff;
  border: none;
}

.form input::placeholder,
.form textarea::placeholder {
  font-family: $font-mono;
  font-size: 16px;
  color: #ffffff;
  opacity: 1;
}

.form input {
  max-width: 270px;
  width: calc(50% - 20px);
  min-height: 50px;
}

.form input:first-child {
  margin-right: 30px;
}

.form textarea {
  font-family: $font-mono;
  font-size: 16px;
  margin-right: 10px;
  width: calc(100% - 10px);
  max-width: 545px;
  min-height: 154px;
  margin-top: 40px;
  margin-bottom: 43px;
  resize: none;
}

.form button {
  color: #ffffff;
  max-width: 270px;
  width: calc(50% - 30px);
  border-radius: 5px;
  height: 45px;
  border: 1px solid #ffffff;
  background-color: $bg-blue;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  margin-right: 38px;
  cursor: pointer;
}

.form button:hover {
  opacity: 0.8;
  transition: all 0.5s linear;
}

.form input[type="checkbox"] {
  width: 0;
  height: 0;
  opacity: 0;
  position: absolute;
}

.form label {
  font-family: $font-mono;
  font-size: 14px;
  line-height: 16px;
  display: flex;
  width: calc(50% - 30px);
  opacity: 0.8;
  cursor: pointer;
  outline: none;
}

.form label:hover {
  opacity: 1;
  transition: all 0.5s linear;
}

.form input[type="checkbox"] + label::before {
  content: "";
  display: block;
  min-width: 16px;
  height: 16px;
  border: 1px solid #ffffff;
  margin-right: 16px;
}

input[type="checkbox"]:focus + label {
  opacity: 1;
  transition: all 500ms linear;
}

.form input[type="checkbox"]:checked + label::before {
  background-image: url("../img/checkbox-on.svg");
  background-position: center center;
  background-repeat: no-repeat;
}

@media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
  .form {
    background-image: url("../img/airplane-desktop@2x.png"), url("../img/triangle-group@2x.png");
  }
}

@media (max-width: 1023px) {
  .form {
    width: calc(100% - 180px);
    padding: 0 90px;
    background-image: none;
    padding-bottom: 100px;
  }

  .form form,
  .form h2,
  .form p {
    position: static;
    width: 100%;
    margin: 0 auto;
    max-width: none;
  }

  .form h2 {
    padding-top: 82px;
    font-size: 32px;
  }

  .form input {
    max-width: none;
    width: calc(50% - 15px);
    height: 52px;
  }

  .form textarea {
    width: 100%;
    max-width: none;
    min-height: 160px;
    margin-top: 45px;
    margin-right: 0;
  }

  .form p {
    font-size: 16px;
    margin-top: 13px;
    margin-bottom: 50px;
  }

  .form label {
    width: calc(50% - 15px);
    max-width: none;
  }

  .form button {
    width: calc(50% - 15px);
    margin-right: 30px;
    max-width: none;
  }
}

@media (max-width: 767px) {
  .form {
    width: calc(100% - 30px);
    padding: 0 15px;
    background-image: none;
    padding-bottom: 40px;
  }

  .form input,
  .form textarea {
    width: 100%;
  }

  .form form,
  .form h2,
  .form p {
    position: static;
    width: 100%;
    margin: 0 auto;
  }

  .form h2 {
    padding-top: 25px;
    line-height: 140%;
    font-size: 28px;
  }

  .form p {
    margin-top: 15px;
    margin-bottom: 25px;
  }

  .form input:first-child {
    margin-right: 0;
  }

  .form label {
    width: 100%;
  }

  .form input {
    margin-top: 0;
    margin-bottom: 31px;
  }

  .form textarea {
    min-height: 104px;
    margin-top: 0;
    margin-bottom: 30px;
  }

  .form button {
    margin-right: 0;
    max-width: none;
    width: 100%;
    margin-bottom: 30px;
  }
}
